"use client"

import { useState, useRef, useEffect } from "react"
import { Copy, MapPin, ArrowLeft } from "lucide-react"
import { cn } from "@/lib/utils"

interface CookingInterfaceProps {
  recipe: {
    recipe_name: string
    ingredients: string[]
    instructions: string[]
  }
  onBack: () => void
}

export function CookingInterface({ recipe, onBack }: CookingInterfaceProps) {
  const [activeTab, setActiveTab] = useState<"ingredients" | "directions">("ingredients")
  const [checkedIngredients, setCheckedIngredients] = useState<Record<number, boolean>>({})
  const [currentStepIndex, setCurrentStepIndex] = useState(0)
  // Store timers as a map: "stepIndex-partIndex" -> { remaining: number, isRunning: boolean, original: number }
  const [timers, setTimers] = useState<Record<string, { remaining: number, isRunning: boolean, original: number }>>({})

  const directionsContainerRef = useRef<HTMLDivElement>(null)
  const stepRefsRef = useRef<(HTMLDivElement | null)[]>([])

  // For TikTok-style snap scroll
  const isSnappingRef = useRef(false)
  const lastScrollYRef = useRef(0)
  const wheelTimeoutRef = useRef<NodeJS.Timeout | null>(null)

  const extractTimer = (text: string): number | null => {
    const match = text.match(/(\d+)\s*(minutes?|mins?|hours?|hrs?)/i)
    if (!match) return null
    const val = parseInt(match[1])
    const unit = match[2].toLowerCase()
    if (unit.includes("hour")) return val * 3600
    return val * 60
  }

  const formatTime = (seconds: number): string => {
    const m = Math.floor(seconds / 60)
    const s = seconds % 60
    return `${m.toString().padStart(2, "0")}:${s.toString().padStart(2, "0")}`
  }

  // Timer Logic - Decrement all running timers
  useEffect(() => {
    const interval = setInterval(() => {
      setTimers((prev) => {
        const next = { ...prev }
        let hasChanges = false

        Object.keys(next).forEach((key) => {
          if (next[key].isRunning && next[key].remaining > 0) {
            next[key] = { ...next[key], remaining: next[key].remaining - 1 }
            hasChanges = true
            
            // Auto-pause when reaching 0
            if (next[key].remaining === 0) {
              next[key].isRunning = false
            }
          }
        })

        return hasChanges ? next : prev
      })
    }, 1000)

    return () => clearInterval(interval)
  }, [])

  const getStepIngredients = (step: string): string[] =>
    recipe.ingredients.filter((ing) => step.toLowerCase().includes(ing.split(",")[0].toLowerCase()))

  // Detect active step (keep this untouched)
  useEffect(() => {
    if (activeTab !== "directions") return

    const observer = new IntersectionObserver(
      (entries) => {
        let topIndex = currentStepIndex
        let maxRatio = 0

        for (const entry of entries) {
          if (entry.isIntersecting && entry.intersectionRatio > maxRatio) {
            maxRatio = entry.intersectionRatio
            const idx = Number(entry.target.getAttribute("data-step-index"))
            if (!isNaN(idx)) topIndex = idx
          }
        }

        if (maxRatio > 0.4) setCurrentStepIndex(topIndex)
      },
      { threshold: 0.5 }
    )

    stepRefsRef.current.forEach((r) => r && observer.observe(r))
    return () => observer.disconnect()
  }, [activeTab])

  // üëâ **TikTok Vertical Feed Scroll**
  useEffect(() => {
    if (activeTab !== "directions") return

    const container = directionsContainerRef.current
    if (!container) return

    const onWheel = (e: WheelEvent) => {
      if (isSnappingRef.current) {
        e.preventDefault()
        return
      }

      e.preventDefault() // block default scrolling

      const direction = e.deltaY > 0 ? "down" : "up"

      if (wheelTimeoutRef.current) clearTimeout(wheelTimeoutRef.current)

      wheelTimeoutRef.current = setTimeout(() => {
        isSnappingRef.current = true

        if (direction === "down" && currentStepIndex < recipe.instructions.length - 1) {
          stepRefsRef.current[currentStepIndex + 1]?.scrollIntoView({
            behavior: "smooth",
            block: "center",
          })
        } else if (direction === "up" && currentStepIndex > 0) {
          stepRefsRef.current[currentStepIndex - 1]?.scrollIntoView({
            behavior: "smooth",
            block: "center",
          })
        }

        setTimeout(() => {
          isSnappingRef.current = false
        }, 450)
      }, 50)
    }

    container.addEventListener("wheel", onWheel, { passive: false })

    return () => container.removeEventListener("wheel", onWheel)
  }, [activeTab, currentStepIndex])

  const toggleAllIngredients = () =>
    setCheckedIngredients((prev) => {
      const allChecked = recipe.ingredients.every((_, i) => prev[i])
      if (allChecked) return {}
      return Object.fromEntries(recipe.ingredients.map((_, i) => [i, true]))
    })

  const copyIngredients = () => navigator.clipboard.writeText(recipe.ingredients.join("\n"))

  return (
    <div className="min-h-screen flex flex-col bg-gray-100">
      {/* Header */}
      <div className="sticky top-0 bg-white border-b border-neutral-200 px-4 py-4 sm:px-6 z-20">
        <div className="max-w-4xl mx-auto flex items-center justify-center gap-4 relative">
          <button onClick={onBack} className="absolute left-0 text-neutral-900 hover:text-neutral-600 transition-colors">
            <ArrowLeft className="w-6 h-6" />
          </button>

          <div className="flex items-center gap-3">
            <h1 className="text-xl sm:text-2xl font-bold text-neutral-900">{recipe.recipe_name}</h1>
          </div>
        </div>
      </div>

      {/* Tabs */}
      <div className="sticky top-[64px] bg-white border-b border-neutral-200 px-4 sm:px-6 py-3 z-10">
        <div className="max-w-4xl mx-auto flex items-center justify-center gap-8">
          <button
            onClick={() => setActiveTab("ingredients")}
            className={cn(
              "pb-2 text-lg font-semibold transition-all relative",
              activeTab === "ingredients" ? "text-neutral-900" : "text-neutral-500 hover:text-neutral-700"
            )}
          >
            Ingredients
            {activeTab === "ingredients" && <span className="absolute bottom-0 left-0 w-full h-0.5 bg-neutral-900 rounded-full" />}
          </button>

          <button
            onClick={() => setActiveTab("directions")}
            className={cn(
              "pb-2 text-lg font-semibold transition-all relative",
              activeTab === "directions" ? "text-neutral-900" : "text-neutral-500 hover:text-neutral-700"
            )}
          >
            Directions
            {activeTab === "directions" && <span className="absolute bottom-0 left-0 w-full h-0.5 bg-neutral-900 rounded-full" />}
          </button>
        </div>
      </div>

      {/* CONTENT */}
      <div className="flex-1 overflow-hidden">
        {activeTab === "ingredients" ? (
          // -------------------------
          // INGREDIENTS TAB (unchanged)
          // -------------------------
          <div className="px-4 sm:px-6 py-8 max-w-4xl mx-auto">
            <div className="mb-6">
              <div className="flex items-end justify-between mb-2">
                <div>
                  <h2 className="text-2xl font-bold text-neutral-900">Get Ingredients</h2>
                  <p className="text-neutral-500 text-sm mt-1">
                    Select the ingredients you have missing to view nearby store options or add to your list.
                  </p>
                </div>
                <button
                  onClick={toggleAllIngredients}
                  className="text-sm font-semibold text-neutral-900 underline decoration-2 underline-offset-4 hover:text-neutral-600 whitespace-nowrap ml-4"
                >
                  Select All
                </button>
              </div>
            </div>

            <div className="bg-white rounded-3xl p-2 border border-neutral-100 mb-24">
              <div className="space-y-1">
                {recipe.ingredients.map((ingredient, idx) => (
                  <div
                    key={idx}
                    className="group flex items-center gap-4 p-4 rounded-2xl hover:bg-neutral-50 transition-colors cursor-pointer"
                    onClick={() =>
                      setCheckedIngredients((prev) => ({ ...prev, [idx]: !prev[idx] }))
                    }
                  >
                    <div className="flex-1">
                      <p
                        className={cn(
                          "text-lg font-medium transition-all",
                          checkedIngredients[idx] ? "text-neutral-400 line-through" : "text-neutral-900"
                        )}
                      >
                        {ingredient}
                      </p>
                    </div>

                    <div
                      className={cn(
                        "w-8 h-8 rounded-lg border-2 flex items-center justify-center transition-all",
                        checkedIngredients[idx]
                          ? "bg-neutral-900 border-neutral-900"
                          : "border-neutral-200 group-hover:border-neutral-300"
                      )}
                    >
                      {checkedIngredients[idx] && (
                        <svg className="w-5 h-5 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                        </svg>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {/* FABs */}
            <div className="fixed bottom-8 left-1/2 -translate-x-1/2 z-40 flex gap-3">
              <button
                onClick={copyIngredients}
                className="bg-neutral-400 hover:bg-neutral-500 text-white font-semibold py-3 px-8 rounded-full shadow-lg flex items-center gap-2 transition-all"
              >
                Copy
              </button>
              <button className="bg-neutral-900 hover:bg-neutral-800 text-white font-semibold py-3 px-8 rounded-full shadow-lg flex items-center gap-2 transition-all">
               Stores
              </button>
            </div>
          </div>
        ) : (
          // -------------------------
          // DIRECTIONS TAB
          // -------------------------
          <div
            ref={directionsContainerRef}
            className="h-full overflow-y-scroll px-4 sm:px-6 py-8 max-w-4xl mx-auto snap-y snap-mandatory scroll-smooth [&::-webkit-scrollbar]:hidden [-ms-overflow-style:'none'] [scrollbar-width:'none']"
          >
            <div className="pb-32">
              {recipe.instructions.map((instruction, idx) => {
                const isActive = idx === currentStepIndex
                const stepIngredients = getStepIngredients(instruction)

                return (
                  <div
                    key={idx}
                    ref={(el) => (stepRefsRef.current[idx] = el)}
                    data-step-index={idx}
                    className={cn(
                      "transition-all duration-500 rounded-3xl overflow-hidden snap-center snap-always mb-6 last:mb-0",
                      "min-h-[calc(100vh-280px)] h-[calc(100vh-280px)]",
                      isActive
                        ? "bg-white scale-100 opacity-100"
                        : "bg-neutral-200 scale-95 opacity-50 cursor-pointer hover:opacity-70"
                    )}
                    onClick={() =>
                      !isActive &&
                      stepRefsRef.current[idx]?.scrollIntoView({ behavior: "smooth", block: "center" })
                    }
                  >
                    <div className="p-8 h-full flex flex-col">
                      <div className="flex items-center gap-2 mb-4">
                        <div
                          className={cn(
                            "px-3 py-1 rounded-lg flex items-center gap-2",
                            isActive ? "bg-neutral-100" : "bg-neutral-300"
                          )}
                        >
                          <span className="font-bold text-neutral-900">Step {idx + 1}</span>
                          <span className="text-neutral-500">‚Üó</span>
                        </div>
                      </div>

                      {isActive && stepIngredients.length > 0 && (
                        <div className="flex flex-wrap gap-2 mb-6">
                          {stepIngredients.map((ing, i) => (
                            <span
                              key={i}
                              className="px-3 py-1 bg-neutral-100 rounded-lg text-sm text-neutral-600 font-medium"
                            >
                              {ing.split(",")[0]}
                            </span>
                          ))}
                        </div>
                      )}

                      <p
                        className={cn(
                          "font-medium transition-all",
                          isActive
                            ? "text-3xl sm:text-4xl text-neutral-900 leading-[1.5]"
                            : "text-lg text-neutral-600 line-clamp-3 leading-relaxed"
                        )}
                      >
                        {isActive
                          ? instruction.split(/(\d+\s*(minutes?|mins?))/gi).map((part, i) => {
                              if (/\d+\s*(minutes?|mins?)/i.test(part)) {
                                const t = extractTimer(part)
                                const timerKey = `${idx}-${i}`
                                const timerState = timers[timerKey]
                                const hasRemainingTime = timerState && timerState.remaining > 0
                                
                                return (
                                  <span
                                    key={i}
                                    onClick={(e) => {
                                      e.stopPropagation()
                                      if (t) {
                                        setTimers(prev => {
                                          const current = prev[timerKey]
                                          if (current) {
                                            // Toggle running state
                                            return {
                                              ...prev,
                                              [timerKey]: { ...current, isRunning: !current.isRunning }
                                            }
                                          } else {
                                            // Initialize and start
                                            return {
                                              ...prev,
                                              [timerKey]: { remaining: t, isRunning: true, original: t }
                                            }
                                          }
                                        })
                                      }
                                    }}
                                    className={cn(
                                      "font-bold cursor-pointer inline-flex items-center gap-2 px-3 py-1 rounded-2xl transition-all",
                                      hasRemainingTime
                                        ? "bg-green-50 text-green-600 border-2 border-green-200"
                                        : "text-green-600 hover:bg-green-50"
                                    )}
                                  >
                                    {hasRemainingTime ? (
                                      <span className="font-mono">
                                        {formatTime(timerState.remaining)}
                                        {!timerState.isRunning && " (Paused)"}
                                      </span>
                                    ) : (
                                      part
                                    )}
                                  </span>
                                )
                              }
                              return part
                            })
                          : instruction}
                      </p>
                    </div>
                  </div>
                )
              })}
            </div>
          </div>
        )}
      </div>

      {/* Progress Bar */}
      {activeTab === "directions" && (
        <div className="fixed bottom-8 left-1/2 -translate-x-1/2 z-40 w-full max-w-md px-4">
          <div className="bg-white/90 backdrop-blur-xl border border-neutral-200 shadow-2xl rounded-2xl p-4">
            <div className="flex items-center justify-between mb-3">
              <span className="text-sm font-semibold text-neutral-900">
                Step {currentStepIndex + 1}{" "}
                <span className="text-neutral-400">of {recipe.instructions.length}</span>
              </span>
              <span className="text-xs font-bold text-neutral-900">
                {Math.round(((currentStepIndex + 1) / recipe.instructions.length) * 100)}%
              </span>
            </div>

            <div className="flex gap-1.5 h-3">
              {recipe.instructions.map((_, i) => (
                <div
                  key={i}
                  className={cn(
                    "h-full flex-1 rounded-full transition-all duration-300",
                    i <= currentStepIndex ? "bg-neutral-900" : "bg-neutral-100"
                  )}
                />
              ))}
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
